










import { Component, Vue } from 'vue-property-decorator';

import Tutorial from './components/Tutorial.vue';
import Editor from './components/Editor.vue';
import Viewer from './components/Viewer.vue';

@Component({
    components: {
        Tutorial,
        Editor,
        Viewer,
    }
})
export default class App extends Vue {
    public created() {
        const pageData = document.querySelector('script#json-representation');
        if (pageData) {
            console.log(JSON.parse(atob(pageData.innerHTML)));
            this.$store.dispatch('load', JSON.parse(atob(pageData.innerHTML)));
        }
    }
}
